html {
  font-family: "Montserrat";
  font-size: 10px;
  font-style: normal;
  font-weight: normal;
  -webkit-animation: bugfix infinite 1s;
  line-height: 1.2;
  margin: 0;
  padding: 0;
}

body {
  font-style: normal;
  font-weight: normal;
  -webkit-animation: bugfix infinite 1s;
  line-height: 1.2;
  margin: 0;
  padding: 0;
  color: #262525;
  background-color: #FFFFFF;
}

input,
textarea {
  -webkit-animation: bugfix infinite 1s;
  line-height: inherit;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  color: inherit;
}

a {
  color: unset;
}

a,
a:hover {
  text-decoration: none;
}

button,
input,
a,
textarea {
  outline: none;
  cursor: pointer;
  font: inherit;
}
button:focus,
input:focus,
a:focus,
textarea:focus {
  outline: none;
}
button:active,
input:active,
a:active,
textarea:active {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font: inherit;
  margin: 0;
  padding: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  border: none;
  color: inherit;
  font: inherit;
  text-align: inherit;
  padding: 0;
  background-color: transparent;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  max-width: 1620px;
  width: 95vw;
  margin: 0 auto;
}

section {
  margin-bottom: 150px;
  overflow: hidden;
}
@media (max-width: 768px) {
  section {
    margin-bottom: 6rem;
  }
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

@media (min-width: 1920px) {
  html {
    font-size: 10px;
  }
}
@media (max-width: 768px) {
  body {
    -webkit-text-size-adjust: none;
  }
  .container {
    padding: 0 1.6rem;
    width: 100%;
  }
}
body {
  color: #292929;
  font-family: "Montserrat";
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

a:hover {
  color: #00AAA3;
}
a:hover span {
  color: #00AAA3;
}

.breadcrumbs__item {
  position: relative;
  font-size: 1.6rem;
  line-height: 2rem;
  font-weight: 500;
  color: #8D9091;
}
.breadcrumbs__item:not(:first-child) {
  margin-left: 1.6rem;
}
.breadcrumbs__item:not(:first-child):after {
  content: "\\";
  position: absolute;
  left: -1.1rem;
  color: inherit;
}
.breadcrumbs__item.current {
  color: #292929;
  pointer-events: none;
  cursor: default;
}
@media (max-width: 768px) {
  .breadcrumbs {
    display: none;
  }
}

input, textarea {
  width: 100%;
  background-color: #F6F6F6;
  padding: 1.6rem 2.4rem;
  font-size: 2rem;
  font-weight: 400;
  border: 0.1rem solid transparent;
}
input::placeholder, textarea::placeholder {
  color: #292929;
  opacity: 0.5;
}
@media (max-width: 768px) {
  input, textarea {
    padding: 1.2rem 1rem;
    font-size: 1.6rem;
    line-height: 2.4rem;
  }
}

input[type=file] {
  display: none;
}

form input.invalid {
  border: 0.1rem solid #EE443F;
  color: #C72E37;
  background-color: #FDECEC;
}

.title {
  font-size: 4.8rem;
