  }
}

.text-m {
  font-size: 2rem;
  font-weight: 500;
  line-height: 2.8rem;
  color: #292929;
}
@media (max-width: 768px) {
  .text-m {
    font-size: 1.6rem;
    line-height: 2rem;
  }
}

.text-s {
  font-size: 1.8rem;
  font-weight: 500;
