  }
}

.button, input::file-selector-button {
  display: flex;
  justify-content: center;
  padding: 2rem 3.7rem;
  align-items: center;
  color: #ffffff;
  font-family: "Montserrat";
  font-size: 2.4rem;
  font-weight: 600;
  line-height: 2.8rem;
  width: fit-content;
  background-color: #00AAA3;
  transition: background-color 0.3s;
  cursor: pointer;
}
.button:hover, input::file-selector-button:hover {
  background-color: #0ad6bb;
}
@media (max-width: 768px) {
  .button, input::file-selector-button {
    width: 100%;
    padding: 1.2rem 0.8rem;
    font-size: 1.8rem;
    line-height: 2.4rem;
  }
}

.white {
  background-color: #ffffff;
  color: #292929;
}

.load-file__label {
  min-width: 29.5rem;
  padding: 1.2rem;
}
@media (max-width: 768px) {
  .load-file__label {
    width: 100%;
    min-height: 4.8rem;
    padding: 0.6rem 0.8rem;
  }
}
.load-file__uploaded {
  width: 100%;
  display: flex;
  align-items: center;
}
.load-file__icon {
  width: 3.6rem;
  height: 3.6rem;
  margin-right: 0.8rem;
}
@media (max-width: 768px) {
  .load-file__icon {
    width: 3.2rem;
    height: 3.2rem;
  }
}
.load-file__content {
  display: flex;
  flex-direction: column;
}
.load-file__text {
  overflow: hidden;
  max-width: 19.4rem;
  width: fit-content;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.8rem;
  color: #292929;
}
@media (max-width: 768px) {
  .load-file__text {
    font-size: 1.2rem;
    max-width: 25.6rem;
  }
}

.close {
  width: 2.4rem;
  height: 2.4rem;
  margin: 0 0 0 auto;
  cursor: pointer;
}

.accent {
  color: #00AAA3;
}

.btn-prev.swiper-button-prev,
.btn-next.swiper-button-next {
  position: relative;
  width: 5.6rem;
  height: 5.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s, opacity 0.3s;
  margin: 0;
  background-color: #00AAA3;
  overflow: hidden;
  padding: 0;
}
.btn-prev.swiper-button-prev img,
.btn-next.swiper-button-next img {
  width: 2.4rem;
  height: 2.4rem;
}
.btn-prev.swiper-button-prev:hover,
.btn-next.swiper-button-next:hover {
  background-color: #292929;
}
.btn-prev.swiper-button-prev::after,
.btn-next.swiper-button-next::after {
  display: none;
}
@media (max-width: 768px) {
  .btn-prev.swiper-button-prev,
  .btn-next.swiper-button-next {
    display: none;
  }
}

.btn-next.swiper-button-next img {
  transform: rotate(180deg);
}

.btn-prev.swiper-button-disabled, .btn-next.swiper-button-disabled {
  opacity: 0.5;
  cursor: alias;
}

.swiper-pagination .swiper-pagination-bullet {
  width: 2rem;
  height: 2rem;
  background-color: #C5E7E6;
}
.swiper-pagination .swiper-pagination-bullet-active {
  background-color: #00AAA3;
}
@media (max-width: 768px) {
  .swiper-pagination .swiper-pagination-bullet {
    width: 1.2rem;
    height: 1.2rem;
  }
}

.error {
  position: absolute;
  bottom: -2.4rem;
  display: none;
  font-size: 1.4rem;
  line-height: 1.6rem;
  margin-top: 0.4rem;
}
.error span {
  color: #EE443F;
}
.error__icon {
  position: relative;
  top: 0.2rem;
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
  margin-right: 0.3rem;
}
.error.active {
  display: block;
}

.hidden {
  display: none;
}

.hero {
